.game-started {
  display: flex;
  height: 100%;
  color: white;
  animation: gameboard-actions-fade-in 0.5s linear;
}

.game {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  width: 80%;
  text-align: center;
}

.hand {
  overflow: auto;
  white-space: nowrap;
  padding: 1em;
}

.output {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  padding: 15px;
  text-align: center;
  box-shadow: 0.05em 0.05em 0.2em black;
}

.button {
  cursor: pointer;
  color: white;
  background-color: rgba(116, 34, 34);
  border: 0;
  border-radius: 0.2em;
}

.button:active {
  box-shadow: 0.1em 0.1em 0.4em black;
}

.button:hover {
  background-color: rgb(132, 34, 34);
  transition: 0.2s linear;
}

.button--response {
  width: 6em;
  margin: 0 1em;
  padding: 0.3em 0;
  box-shadow: 0.2em 0.2em 0.5em black;
}

.button--sidebar {
  cursor: pointer;
  font-size: 1em;
  margin: 0.5em;
  border: 0;
  width: 10em;
  padding: 0.2em 0;
  color: white;
  background-color: rgba(116, 34, 34);
  border-radius: 0.2em;
  box-shadow: 0.2em 0.2em 0.5em black;
}

.button-restart {
  background-color: rgb(190, 0, 0);
}

.button-restart:hover {
  background-color: rgb(230, 0, 0);
}

.button--gameboard {
  font-size: 1.2em;
  margin: 0.5em;
  width: 10em;
  padding: 0.5em 0;
  box-shadow: 0.5em 0.5em 0.6em black;
}

.button--gameboard:active {
  box-shadow: 0.3em 0.3em 0.4em black;
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20%;
  border-left: 7px solid rgb(116, 34, 34);
  padding: 10px;
}

.sidebar-actions {
  text-align: center;
}

.deck {
  text-align: center;
}

.card {
  height: 120px;
  cursor: pointer;
  margin: 2px 4px;
  box-shadow: 0.2em 0.2em 0.3em black;
}

.card--player:hover {
  transform: scale(1.05) translateY(-8px);
  box-shadow: 0.2em 0.2em 0.6em black;
}

.card--deck {
  margin-bottom: 20px;
  border-radius: 5px;
  box-shadow: 0.4em 0.4em rgb(154, 154, 154), 0.3em 0.3em rgb(154, 154, 154), 0.2em 0.2em rgb(154, 154, 154), 0.1em 0.1em rgb(154, 154, 154), 0.6em 0.6em 0.3em black;
}

.card--deck:hover {
  transform: scale(1.02);
  box-shadow: 0.4em 0.4em rgb(154, 154, 154), 0.3em 0.3em rgb(154, 154, 154), 0.2em 0.2em rgb(154, 154, 154), 0.1em 0.1em rgb(154, 154, 154), 0.6em 0.6em 0.5em black;

}

.heading {
  margin: 10px 0;
}

.heading--hand{
  margin-bottom: 0;
}

.stats-heading {
  text-decoration: underline;
}

.gameboard-actions {
  position: absolute;
  color: white;
  margin: auto;
  width: 100%;
  text-align: center;
  top: 150px;
  animation: gameboard-actions-fade-in 1s linear;
}

.gameboard-actions > h1 {
  font-size: 3em;
  margin: .5em;
  text-shadow: 2px 2px 1px black;
}

@keyframes gameboard-actions-fade-in {
  0% {
    opacity: 0;
  }
}