.backdrop {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 10;
}

.overlay {
  border-left: 1px white solid;
  border-right: 1px white solid;
  position: absolute;
  width: 65%;
  height: 65%;
  overflow: auto;
  left: 17.5%;
  top: 12%;
  z-index: 20;
  padding-right: 1%;
}

.modalAction {
  position: absolute;
  margin: -10px;
  width: 61%;
  left: 20%;
  text-align: center;
  bottom: 12%;
  z-index: 20;
}

.closeButton {
  cursor: pointer;
  font-size: 1em;
  font-weight: bold;
  border: 0;
  padding: 0.75em 4em;
  color: white;
  background-color: rgba(116, 34, 34, 0.95);
  border-radius: 0.5em;
  box-shadow: 0.2em 0.2em 0.5em black;
}

.closeButton:hover {
  background-color: rgba(172, 51, 51, 0.95);
}

.closeButton:active {
  box-shadow: 0.1em 0.1em 0.4em black;
}

.heading {
  position: absolute;
  width: 100%;
  color: white;
  text-align: center;
  margin: 0;
  top: 0%;
  z-index: 20;
}

.match {
  color: green;
}

.noMatch {
  color: red;
}