.gameboard {
  position: relative;
  border: 7px solid rgb(116, 34, 34);
  border-radius: 10px;
  margin: auto;
  height: 500px;
  width: 1200px;
  background-image: linear-gradient(to bottom right, rgb(0, 120, 0), rgb(0, 50, 0));
  font-size: 0.8em;
  box-shadow: 0.2em 0.2em 0.5em black;
}

@media (min-height: 654px) {
  .gameboard {
    height: 600px;
    font-size: 1em;
  }  
}